.nutrition-program {
  min-height: 265px;
  background-color: @basic-grey;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 0 20px 0 20px;
  margin-top: 23px;
  max-width: 87vw;
  overflow: hidden;

  @media (min-width: @tablet-width) {
    margin-top: 30px;
    min-height: 372px;
    padding: 0 50px;
    padding-left: 34px;
    justify-content: space-evenly;
    max-width: 95vw;

    @media (min-width: @desktop-width) {
      width: 570px;
    }
  }
}

.nutrition-program__title {
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  text-align: center;
  position: relative;
  padding-left: 12px;
  padding-top: 30px;
  font-weight: 400;
  margin: 0;

  &::before {
    content: "";
    width: 50px;
    height: 53px;
    position: absolute;
    top: 23px;
    left: 1px;
    background-repeat: no-repeat;
  }

  @media (min-width: @tablet-width) {
    font-size: 36px;
    line-height: 36px;
    text-align: left;
    margin-bottom: 0;
    padding-left: 20px;
    padding-top: 52px;

    @media (min-width: @desktop-width) {
      text-align: center;
      padding-top: 45px;
    }
  }
}

.nutrition-program__title--one {
  &::before {
    background-image: url("../img/nutrition-program/cat_back.svg");

    @media (min-width: @tablet-width) {
      background-image: none;

      @media (min-width: @desktop-width) {
        background-image: url("../img/nutrition-program/cat_back.svg");
        width: 100px;
        height: 100px;
        background-size: 100px 100px;
        left: 16px;
        top: 10px;
      }
    }
  }
}

.nutrition-program__title--two {
  padding-left: 37px;

  &::before {
    width: 67px;
    left: -10px;
    top: 23px;
    background-image: url("../img/nutrition-program/cat_back-2.svg");
  }

  @media (min-width: @tablet-width) {
    padding-left: 20px;

    &::before {
      background-image: none;
    }

    @media (min-width: @desktop-width) {
      padding-left: 65px;
      padding-top: 45px;

      &::before {
        background-image: url("../img/nutrition-program/cat_back-2.svg");
        height: 100px;
        width: 135px;
        background-size: 135px 100px;
        left: -4px;
        top: 16px;
      }
    }
  }
}

.nutrition-program__description {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  margin: 0;
  flex-grow: 1;
  padding-top: 29px;
  text-align: left;
  padding-right: 8px;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 24px;
    margin: 0;
    flex-grow: 0;
    padding: 0 0 37px 20px;
    position: relative;
    padding-right: 221px;

    &::before {
      content: "";
      position: absolute;
      background-repeat: no-repeat;
      width: 200px;
      height: 200px;
      background-size: 200px 200px;
    }

    @media (min-width: @desktop-width) {
      padding: 40px 0 0 10px;
    }
  }
}

.nutrition-program__description-one {
  @media (min-width: @tablet-width) {
    &::before {
      background-image: url("../../../img/nutrition-program/cat_back.svg");
      right: 10px;
      bottom: -10px;
    }

    @media (min-width: @desktop-width) {
      &::before {
        background-image: none;
      }
    }
  }
}

.nutrition-program__description-two {
  @media (min-width: @tablet-width) {
    padding-right: 264px;
    position: relative;
    padding-bottom: 39px;

    &::before {
      background-image: url("../../../img/nutrition-program/cat_back-2.svg");
      width: 270px;
      background-size: 270px 200px;
      top: -51px;
      right: -22px;
    }

    @media (min-width: @desktop-width) {
      padding: 40px 0 0 10px;

      &::before {
        background-image: none;
      }
    }
  }
}

.nutrition-program__button {
  font-family: "oswald", sans-serif;
  align-items: center;
  text-decoration: none;
  text-transform: uppercase;
  color: @basic-black;
  height: 49px;
  display: flex;
  border-top: 1px solid @special-grey-two;
  position: relative;
  padding-bottom: 8px;

  &:active {
    opacity: 30%;
  }

  &::after {
    position: absolute;
    content: "";
    width: 26px;
    height: 12px;
    background-image: url("../img/nutrition-program/icon-arrow.svg");
    left: 100px;
    background-repeat: no-repeat;
    background-position: right center;
    transition-duration: 400ms;
  }

  @media (min-width: @tablet-width) {
    font-size: 20px;
    border: 0;
    padding-bottom: 42px;
    padding-left: 20px;

    &::after {
      left: 151px;
    }

    @media (min-width: @desktop-width) {
      padding-bottom: 31px;
      padding-left: 11px;
    }
  }
}

.nutrition-program__button:hover::after {
  width: 32px;
}

.nutrition-program__button--two {
  @media (min-width: @tablet-width) {
    &::after {
      left: 147px;
    }
  }
}
